import React, {useState} from 'react'
import Input from "@material-ui/core/Input";
import Button from "@material-ui/core/Button";

import {styled} from '@material-ui/core/styles';


const MusicSelect = () => {
    const [fileName, setFileName] = useState(localStorage.getItem('musicName') === null ? "" : localStorage.getItem('musicName'));

    const manageChange = (e) => {
        let file = e.target.files[0];
        if (file.type && !file.type.startsWith('audio/')) {
            console.log('File is not an audio file.', file.type, file);
            return;
        }
        try {
            setFileName(file.name);
            localStorage.setItem('musicName', file.name);
            let fileData = new FileReader();
            fileData.onload = async (e) => saveMusicToLocalStorage(e);
            fileData.readAsDataURL(file);
        }catch (e) {
            console.error(e);
            setFileName(e);
        }
    };


    const saveMusicToLocalStorage = (e) => {
        localStorage.setItem('musicData', e.target.result);
        console.warning(e.target.result);
        setFileName(fileName + " " + e.target.result === null);
        global.refreshCanvas();

    };


    const Input = styled('input')({
        display: 'none',
    });

    return (
        <>
            <label htmlFor="contained-button-file" style={{float: "right", marginBottom: "20px"}}>
                <span className="music-text">{fileName}</span>
                <Input id="contained-button-file" type="file"
                       onChange={e => manageChange(e)}/>
                <Button className="bottom-button" variant="outlined" component="span">
                    Select Song
                </Button>
            </label>
        </>
    )

};

export default MusicSelect;