import React, {useState} from 'react'
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import TextField from "@material-ui/core/TextField";


const LineCircleSize = (classes) => {

    const [lCircleSize, setLCircleSize] = useState(localStorage.getItem('lineCircleSize') === null ? 60 : localStorage.getItem('lineCircleSize'));

    const updateMargin = (e) => {
        try {
            if (e.target.value < 0) {
                setLCircleSize(0);
                localStorage.setItem('lineCircleSize', '0');
            } else if (e.target.value > 1000) {
                setLCircleSize(1000);
                localStorage.setItem('lineCircleSize', '1000');
            } else {
                setLCircleSize(parseInt(e.target.value));
                if (e.target.value === '') {
                    localStorage.setItem('lineCircleSize', '0');
                } else {
                    localStorage.setItem('lineCircleSize', '' + parseInt(e.target.value));
                }
            }
        } catch (e) {
            setLCircleSize(0);
            localStorage.setItem('lineCircleSize', '1');
        }
    };


    return (
        <FormControl className={"all-form-inputs " + classes.formControl}>
            <TextField type="number" id="standard-basic" label="Line CircleSize" value={lCircleSize}
                       onChange={updateMargin}/>
        </FormControl>
    )
};

export default LineCircleSize;