import React, {useState} from 'react'
import Input from "@material-ui/core/Input";
import Button from "@material-ui/core/Button";

import {styled} from '@material-ui/core/styles';


const SwitchMicMusic = () => {
    const [micOrSound, setFileName] = useState(localStorage.getItem('micOrSound') === null ? "mic" : localStorage.getItem('micOrSound'));

    const changeInput = () => {
        if (micOrSound === "mic") {
            setFileName("sound");
            localStorage.setItem('micOrSound', "sound");
        } else {
            setFileName("mic");
            localStorage.setItem('micOrSound', "mic");
        }
        global.refreshCanvas();
    };

    return (
        <label style={{float: "right"}}>
            {micOrSound === "sound" &&
            <span className="music-text">Info: Press on the Canvas to Play/Pause</span>}
            <Button className="bottom-button" style={{marginRight: "20px"}} variant="outlined"
                    component="span" onClick={changeInput}>
                {micOrSound === "mic" ? "Switch to a Sound File instead of Microphone Input" : "Switch to Microphone Input instead of the Sound File"}
            </Button>
        </label>

    )

};

export default SwitchMicMusic;